import {designComponent} from "../../use/designComponent";
import {useSlots} from "../../use/useSlots";
import {useScopedSlots} from "../../use/useScopedSlots";
import {TabCollector} from "./tabs";
import {useRefs} from "../../use/useRefs";

export const PlTab = designComponent({
    name: 'pl-tab',
    props: {
        title: {type: String},                              // 
        init: {type: Boolean},                              // 
        val: {type: [String, Number]},                      //  pl-tabs 
        destroyOnHide: {type: Boolean},                     // ()
    },
    setup({props}) {
        const {refs} = useRefs({
            el: HTMLSpanElement,
        })
        const {slots} = useSlots()
        const {scopedSlots} = useScopedSlots({
            head: {active: Boolean},
        })
        TabCollector.child({sort: () => refs.el})

        return {
            refer: {
                slots,
                scopedSlots,
                props,
            },
            render: () => (<span ref="el">{props.title}</span>)
        }
    },
})

export type PlTabComponent = typeof PlTab.use.class