import {StyleProperties, VNodeChild} from "../../../../shims";
import {TablePlc, TableRenderScope} from "./plc.type";
import {TablePlcAlign, TablePlcFixedType} from "../../core/table.utils";
import {PropType} from 'vue';
import {TableNode} from "../../core/useTableNode";
import {MultipleClass} from "../../../../use/useClasses";
import {FormRule} from "../../../form/form.validate";

export type PlcRenderFunction = (scope: TableRenderScope) => VNodeChild
export type PlcPropsHead = (scope: { plc: TablePlc }) => VNodeChild
export type PlcPropsDefault = PlcRenderFunction
export type PlcPropsSummary = PlcRenderFunction
export type PlcPropsEdit = PlcRenderFunction
export type PlcPropsRenderAfterRow = PlcRenderFunction
export type PlcPropsEditable = boolean | ((node: TableNode) => boolean)

export const PlcGroupProps = {
    title: {type: String},                                                  // 
    align: {type: String as PropType<TablePlcAlign>},                       // 
    noPadding: {type: Boolean},                                             // 
    colDraggable: {type: Boolean, default: null},                           // 

    hide: {type: Boolean},                                                  // 
    order: {type: [String, Number]},                                        // 
    fixed: {type: String as PropType<TablePlcFixedType>, default: TablePlcFixedType.center},// leftrightundefined
    autoFixedLeft: {type: Boolean},                                         // 
    autoFixedRight: {type: Boolean},                                        // 

    headCls: {},                                                            // class
    bodyCls: {},                                                            // class
}

export const PlcProps = {
    ...PlcGroupProps,
    field: {type: String},                                                  // 
    width: {type: [String, Number], default: 120},                          // 
    fit: {type: Boolean},                                                   // ()
    notFitVirtual: {type: Boolean},                                         // 

    // 
    head: {type: Function as PropType<PlcPropsHead>},                       // 
    default: {type: Function as PropType<PlcPropsDefault>},                 // 
    summary: {type: Function as PropType<PlcPropsSummary>},                 // 
    edit: {type: Function as PropType<PlcPropsEdit>},                       // 
    renderAfterRow: {type: Function as PropType<PlcPropsRenderAfterRow>},   // 

    // 
    required: {type: Boolean},                                              // 
    rules: {type: [Object, Array] as PropType<FormRule | FormRule[]>},      // 
    editable: {type: [Boolean, Function] as PropType<PlcPropsEditable>, default: true},// 
    addEditPadding: {type: Boolean},                                        // edit
}

export type PlcPublicAttrsType = {
    level: number,                      // 
    rowspan: number,                    // tdrowspan
    colspan: number,                    // tdcolspan
    isLastFixedLeft: boolean,           // 
    isFirstFixedRight: boolean,         // 
    fixedPosition: {                    // sticky
        left: number,
        right: number,
    },
    // style
    styles: {
        head: StyleProperties,
        body: StyleProperties,
    },
    // class
    classes: {
        head: MultipleClass,
        body: MultipleClass,
    },
}

/**
 * PlcPlcGroup
 * @author  
 * @date    2020/12/18 14:41
 */
export const PlcPublicAttrs: PlcPublicAttrsType = {
    level: 0,
    rowspan: 1,
    colspan: 1,
    isLastFixedLeft: false,
    isFirstFixedRight: false,
    fixedPosition: {
        left: 0,
        right: 0,
    },
    styles: {
        head: {},
        body: {},
    },
    classes: {
        head: [],
        body: [],
    },
}