import {StyleProps} from "../../../use/useStyle";
import {SimpleObject, StyleProperties} from "../../../shims";
import {PropType} from 'vue';
import {Plc, TablePlc} from "../plc/core/plc.type";
import {TableNode} from "./useTableNode";
import {MultipleClass} from "../../../use/useClasses";
import {FormAssociateFields, FormComponentRules} from "../../form/form.validate";
import {useTree} from "../../tree/core/useTree";

export type TablePropsConfig = (plcList: TablePlc[]) => Record<string, any>

export type TablePropsRowClassFunc = (node: TableNode) => MultipleClass
export type TablePropsCellClassFunc = (node: TableNode, plc: Plc) => MultipleClass
export type TablePropsCellStyleFunc = (node: TableNode, plc: Plc) => StyleProperties
export type TablePropsHeadCellClassFunc = (plc: TablePlc) => MultipleClass
export type TablePropsHeadCellStyleFunc = (plc: TablePlc) => StyleProperties

export type TablePropsSpanMethod = (socpe: { node: TableNode, plc: Plc }) => { rowspan: number, colspan: number }

export enum TablePlcAlign {
    left = 'left',
    center = 'center',
    right = 'right',
}

export enum TablePlcFixedType {
    left = 'left',
    center = 'center',
    right = 'right',
}

export const enum TableHoverPart {
    body = 'body',
    head = 'head'
}

export const TableProps = {

    /*---------------------------------------basic-------------------------------------------*/

    ...StyleProps,

    loading: {type: Boolean, default: null},                    // 
    virtual: {type: Boolean, default: false},                   // 
    summaryData: {type: Array as PropType<SimpleObject[]>},     // 
    config: {type: Function as PropType<TablePropsConfig>},     // 
    rules: {type: Object as PropType<FormComponentRules>},      // 
    associateFields: {type: Object as PropType<FormAssociateFields>},   // keyvaluekeyvalue
    colDraggable: {type: Boolean},                              // 
    spanMethod: {type: Function as PropType<TablePropsSpanMethod>},// 

    /*---------------------------------------style-------------------------------------------*/

    border: {type: Boolean},                                    // 
    stripe: {type: Boolean},                                    // table
    hideHeader: {type: Boolean},                                // 
    disabledStickyCompatible: {type: Boolean},                  // iesticky
    headRowHeight: {type: [String, Number], default: 45},       // 
    bodyRowHeight: {type: [String, Number], default: 40},       // 
    showRows: {type: Number, default: 10},                      // 

    rowClassFunc: {type: Function as PropType<TablePropsRowClassFunc>},//  className 
    cellClassFunc: {type: Function as PropType<TablePropsCellClassFunc>},//  className 
    cellStyleFunc: {type: Function as PropType<TablePropsCellStyleFunc>},//  style 
    headCellClassFunc: {type: Function as PropType<TablePropsHeadCellClassFunc>},//  className 
    headCellStyleFunc: {type: Function as PropType<TablePropsHeadCellStyleFunc>},//  style 

    /*---------------------------------------tree-------------------------------------------*/
    ...useTree.createProps<TableNode>(),
    childrenField: {type: String, default: 'children'},         // 
}