import {ExtractPropTypes, PropType} from 'vue';
import {PDate} from "./plainDate";
import {VNodeChild} from "../../shims";

/**
 * 
 * @author  
 * @date    2021/1/18 10:43
 */
export enum DateView {
    year = 'year',
    month = 'month',
    date = 'date',
    time = 'time',
}

export enum DatePanel {
    year = 'year',
    month = 'month',
    date = 'date',
    week = 'week',
    quarter = 'quarter',
}

/**
 * 
 * @author  
 * @date    2021/1/18 10:43
 */
export enum SlideTransitionDirection {
    next = 'next',
    prev = 'prev'
}

/**
 * modelValuestartend
 * @author  
 * @date    2021/1/18 10:44
 */
export const enum DateEmitRangeType {
    start = 'start',
    end = 'end',
}

/**
 * 
 * @author  
 * @date    2021/1/18 10:46
 */
export const DefaultDateFormatString = {
    year: 'YYYY',
    month: 'YYYY-MM',
    date: 'YYYY-MM-DD',
    datetime: 'YYYY-MM-DD HH:mm:ss',
    week: 'YYYY-MM-DD',
    Hms: 'HH:mm:ss',
    quarter: 'YYYYww',
}

export const getDefaultDateFormatter = (() => {
    function getDefaultDateValueFormat(panel: DatePanel, datetime?: boolean) {
        switch (panel) {
            case DatePanel.year:
                return DefaultDateFormatString.year
            case DatePanel.month:
                return DefaultDateFormatString.month
            case DatePanel.date:
                return datetime ? DefaultDateFormatString.datetime : DefaultDateFormatString.date
            case DatePanel.week:
                return DefaultDateFormatString.date
            case DatePanel.quarter:
                return DefaultDateFormatString.month
        }
    }

    function getDefaultDateDisplayFormat(panel: DatePanel, datetime?: boolean) {
        switch (panel) {
            case DatePanel.year:
                return DefaultDateFormatString.year
            case DatePanel.month:
                return DefaultDateFormatString.month
            case DatePanel.date:
                return datetime ? DefaultDateFormatString.datetime : DefaultDateFormatString.date
            case DatePanel.week:
                return 'ggggww'
            case DatePanel.quarter:
                return 'YYYYQ'
        }
    }

    return (panel: DatePanel, datetime?: boolean) => ({
        displayFormat: getDefaultDateDisplayFormat(panel, datetime),
        valueFormat: getDefaultDateValueFormat(panel, datetime),
    })
})();

export const DateViewSeq = {
    year: 1,
    month: 2,
    date: 3,
    time: 4,
}

export const DatePublicProps = {
    modelValue: {type: [String, Array] as PropType<string | string[]>},         // 
    start: {type: String},                                                      // 
    end: {type: String},                                                        // 
    displayFormat: {type: String},                                              // 
    valueFormat: {type: String},                                                // 
    max: {type: String},                                                        // 
    min: {type: String},                                                        // 
    range: {type: Boolean},                                                     // multiple
    multiple: {type: Boolean},                                                  //  range

    firstWeekDay: {type: Number, default: 1},                                   // 01
    defaultTime: {type: String},                                                // 
    defaultStartTime: {type: String},                                           // 
    defaultEndTime: {type: String},                                             // 

    showQuarter: {type: Boolean},                                               // 
    isQuarterActive: {type: Function as PropType<(did: DateItemData) => boolean>},// 

    /*inner props*/
    direction: {type: String, default: 'horizontal'},                           //  pl-transition-slide horizontalvertical
    view: {type: String as PropType<DateView>, default: DateView.month},        // 
    selectDate: {type: Object as PropType<PDate>},                              // 
    datetime: {type: Boolean},                                                  // 
}

/**
 * 
 * @author  
 * @date    2021/1/18 10:45
 */
export type DatePublicPropsType = ExtractPropTypes<typeof DatePublicProps>

/**
 * 
 * @author  
 * @date    2021/1/18 10:45
 */
export const DatePublicEmits = {
    onUpdateModelValue: (val?: string | string[], rangeType?: DateEmitRangeType) => true,
    onUpdateStart: (val?: string) => true,
    onUpdateEnd: (val?: string) => true,
    onUpdateView: (view: DateView) => true,
}

/**
 * 
 * @author  
 * @date    2021/1/18 16:04
 */
export interface DateItemData {
    label: string | number,
    active: boolean,
    now: boolean,
    disabled: boolean,
    start: boolean,
    hover: boolean,
    end: boolean,
    clickable: boolean,
    pd: PDate,
    range: boolean,
    externals?: any,
}

export function DatePanelWrapper(slots: {
    left?: VNodeChild,
    center: VNodeChild,
    right?: VNodeChild,
    content: VNodeChild,
    bodyAttrs?: any,
}) {
    return (
        <div class="pl-date-base-panel">
            <div class="pl-date-base-panel-header">
                <div>{slots.left}</div>
                <div>{slots.center}</div>
                <div>{slots.right}</div>
            </div>
            <div class="pl-date-base-panel-body" {...(slots.bodyAttrs || {})}>
                {slots.content}
            </div>
        </div>
    ) as any
}

export function DatePanelItemWrapper(
    {
        Node,
        item,
        onClick,
        onMouseenter,
    }: {
        Node: any,                                      // 
        item: DateItemData,                             // item 
        onClick: (item: DateItemData) => void,          // item
        onMouseenter: (item: DateItemData) => void,     // item
    }) {

    let listener = {} as any;
    item.clickable && (listener.onClick = () => onClick(item));
    !item.disabled && (listener.onMouseenter = () => onMouseenter(item));

    return (
        <Node
            {...listener}
            class={[
                'pl-date-base-panel-item',
                {
                    'pl-date-base-panel-item-active': item.active,
                    'pl-date-base-panel-item-now': item.now,
                    'pl-date-base-panel-item-disabled': item.disabled,
                    'pl-date-base-panel-item-hover-start': item.start,
                    'pl-date-base-panel-item-hover': item.hover,
                    'pl-date-base-panel-item-hover-end': item.end,
                }
            ]}>
            <div>
                <span>{item.label}</span>
            </div>
        </Node>
    )
}