import {designComponent} from "../../use/designComponent";
import {VNodeChild} from "../../shims";

/**
 * root service
 * referisShowisOpenservice
 * isShowtruefalse
 * isOpentrueisShowfalseisOpentrue
 * isShowisOpenfalseservice
 *
 * @author  
 * @date    2020/11/26 9:24
 */
export function createDefaultService<Option extends object, Refer extends {
    isShow: { value: boolean },
    isOpen: { value: boolean },
    service: (optoin: Option) => void
}>
(
    {
        name,
        setup,
        components,
    }: {
        name: string,
        setup: (option: Option) => {
            refer: Refer,
            render: () => VNodeChild,
        },
        components?: any,
    }
) {
    return designComponent({
        name: `pl-${name}-service`,
        components,
        props: {
            option: {type: Object, required: true}
        },
        setup({props}) {
            const option = props.option as Option
            const {render, refer} = setup(option)
            refer.service(option)
            return {
                render, refer
            }
        },
    })
}